<template>
	<div>
		<vue-headful title="Tienda - Ink&Tales" />
		<section class="uk-section uk-section-muted uk-section-small">
			<div class="uk-container uk-container-large">
				<h1 class="uk-h2 uk-margin-remove">{{'SHOP' | translate}}</h1>
				<p class="uk-text-lead uk-margin-remove-top">Lorem ipsum dolor sit amet, consectetur adipisicing elit</p>
				<!-- <ul class="uk-breadcrumb uk-margin-small-top uk-flex-center">
					<li><router-link to="/" >{{'HOME' | translate}}</router-link></li>
					<li><span>{{'SHOP' | translate}}</span></li>
				</ul> -->
			</div>
		</section>
		<section class="uk-section">
			<div class="uk-container uk-container-large" id="container">
				<header class="uk-text-center">
					<h2>Nuestros planes</h2>
					<p>This is an example of Checkout Pro integration of Mercado Pago</p>
				</header>
				<div class="uk-child-width-1-2@m uk-child-width-1-3@l uk-grid uk-flex-center" uk-grid>
					<div v-for="plan in planes" :key="plan.id">
						<article class="uk-card uk-card-body uk-card-primary uk-text-center">
							<h3 class="uk-h2 uk-margin-remove-bottom"><span :id="'product-description-' + plan.id">{{plan.description}}</span></h3>
							<p class="uk-text-success uk-h3 uk-margin-small-top">$ <span :id="'unit-price-' + plan.id">{{plan.price}}</span></p>
							<div :id="'button-checkout-' + plan.id"></div>
						</article>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
	export default {
		name: "Shop",
		mounted: function() {
			for (var i = this.planes.length - 1; i >= 0; i--) {
				this.loadCheckout(this.planes[i]);
			}
		},
		methods: {
			loadCheckout(plan) {
				var orderData = {
					quantity: 1,
					description: document.getElementById("product-description-" + plan.id).innerHTML,
					price: document.getElementById("unit-price-" + plan.id).innerHTML,
					planid: plan.id
				};

				fetch(process.env.VUE_APP_STRAPI_API_URL + "/create_preference", {
					method: "POST",
					headers: {
						"Content-Type": "application/json",
					},
					body: JSON.stringify(orderData),
				})
				.then(function(response) {
					return response.json();
				})
				.then(function(preference) {
					var script = document.createElement("script");
					/* The source domain must be completed according to the site for which you are integrating. For example: for Argentina ".com.ar" or for Brazil ".com.br". */
					script.src = "https://www.mercadopago.com.ar/integrations/v1/web-payment-checkout.js";
					script.type = "text/javascript";
					script.dataset.preferenceId = preference.id;
					document.getElementById("button-checkout-" + plan.id).innerHTML = "";
					document.querySelector("#button-checkout-" + plan.id).appendChild(script);
				})
			}
		},
		data() {
			return {
				planes: [{
					id: 1,
					description: "Plan Mensual",
					price: 200,
				},
				{
					id: 2,
					description: "Plan Anual",
					price: 5000,
				}
				]
			}
		}
	};
</script>

